* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  overflow: hidden;
  clip: rect(0 0 0 0);
}
.body {
  margin: 0;
  padding: 0;
  background-color: #000000;
}
.demo-canvas {
  pointer-events: none;
}
.main {
  position: relative;
  width: 100%;
  height: 100vh;
  overflow: hidden;
  background-size: cover;
  background-position: center center;
  z-index: 2;
  background: #333;
}
.main:after {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 50%;
  height: 400px;
  background: linear-gradient(286deg, rgba(27, 231, 255, 0.5) 25.99%, #e47aff 80.62%, rgba(100, 19, 255, 0.6) 80.2%);
  background-size: 100%;
  border-radius: 100%;
  filter: blur(100px);
}
@media (max-width: 600px) {
  .main:after {
    display: none;
  }
}
.links-list {
  z-index: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 40px;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate3d(-50%, -50%, 0);
  transform: translate3d(-50%, -50%, 0);
  list-style: none;
}
.links-list__link {
  cursor: pointer;
  text-decoration: none;
}
.links-list__title {
  font-family: 'Roboto Mono', monospace, 'serif';
  font-size: 60px;
  color: #f9f1e9;
  text-align: center;
  font-weight: bold;
  background: transparent;
  white-space: nowrap;
  font-weight: 600;
  letter-spacing: 0.1px;
}
@media (max-width: 900px) {
  .links-list__title {
    font-size: 40px;
  }
}
@media (max-width: 600px) {
  .links-list__title {
    font-size: 30px;
  }
}
.printed-text {
  overflow: hidden;
  border-right: 4px solid #ffffff;
}
.printed-text--it {
  width: 11ch;
  animation: printed-text 1.5s steps(10), flashin-border 0.75s step-start infinite;
}
.printed-text--arh {
  width: 19ch;
  animation: printed-text 3s steps(20), flashin-border 0.75s step-start infinite;
}
@keyframes flashin-border {
  0% {
    border-color: #ffffff;
  }
  50% {
    border-color: transparent;
  }
  100% {
    border-color: #ffffff;
  }
}
@keyframes printed-text {
  from {
    width: 0%;
  }
}
.glitch {
  position: relative;
  color: white;
  mix-blend-mode: lighten;
}
.glitch:before,
.glitch:after {
  content: attr(data-text);
  position: absolute;
  top: 0;
  width: 100%;
  background: transparent;
  clip: rect(0, 0, 0, 0);
}
.glitch:before {
  left: -1px;
  text-shadow: 1px 0 rgba(255, 0, 0, 0.7);
}
.glitch:after {
  left: 1px;
  text-shadow: -1px 0 rgba(0, 0, 255, 0.7);
}
.glitch:hover:before,
.glitch-link:focus > .glitch:before {
  text-shadow: 4px 0 rgba(255, 0, 0, 0.7);
  animation: glitch-loop-1 0.8s infinite ease-in-out alternate-reverse;
}
.glitch:hover:after,
.glitch-link:focus > .glitch:after {
  text-shadow: -5px 0 rgba(0, 0, 255, 0.7);
  animation: glitch-loop-2 0.8s infinite ease-in-out alternate-reverse;
}
@keyframes glitch-loop-1 {
  0% {
    clip: rect(36px, 9999px, 9px, 0);
  }
  25% {
    clip: rect(25px, 9999px, 99px, 0);
  }
  50% {
    clip: rect(50px, 9999px, 102px, 0);
  }
  75% {
    clip: rect(30px, 9999px, 92px, 0);
  }
  100% {
    clip: rect(91px, 9999px, 98px, 0);
  }
}
@keyframes glitch-loop-2 {
  0% {
    top: -1px;
    left: 1px;
    clip: rect(65px, 9999px, 119px, 0);
  }
  25% {
    top: -6px;
    left: 4px;
    clip: rect(79px, 9999px, 19px, 0);
  }
  50% {
    top: -3px;
    left: 2px;
    clip: rect(68px, 9999px, 11px, 0);
  }
  75% {
    top: 0px;
    left: -4px;
    clip: rect(95px, 9999px, 53px, 0);
  }
  100% {
    top: -1px;
    left: -1px;
    clip: rect(31px, 9999px, 149px, 0);
  }
}
